<template>
  <div>
    <v-badge
      bordered
      color="error"
      :content="unreadNotificationsLength"
      overlap
    >
      <v-menu offset-y>
        <template v-slot:activator="{ on, attrs }">
          <v-avatar
            size="40px"
            v-bind="attrs"
            @click="readNotifications($axios)"
            v-on="on"
          >
            <v-icon>mdi-bell</v-icon>
          </v-avatar>
        </template>
        <v-list max-width="400px">
          <div v-for="(item, index) in dropdownNotifications" :key="index">
            <v-list-item exact two-line link :to="route(item)">
              <v-icon color="primary" class="mr-2">{{ icon(item) }}</v-icon>
              <v-list-item-title>
                {{ message(item) }}
              </v-list-item-title>
            </v-list-item>
            <v-divider></v-divider>
          </div>
          <v-list-item link to="/dashboard/notifications" class="text-center">
            <!-- <v-list-item-title> -->
            <div class="text-center">All Notifications</div>
            <!-- </v-list-item-title> -->
          </v-list-item>
        </v-list>
      </v-menu>
    </v-badge>
  </div>
</template>

<script>
import { mapState, mapActions, mapGetters } from 'vuex'
export default {
  computed: {
    ...mapState(['notifications']),
    ...mapGetters('notifications', [
      'dropdownNotifications',
      'unreadNotificationsLength'
    ])
  },
  created() {
    this.fetchNotifications(this.$axios)
  },
  methods: {
    ...mapActions('notifications', ['fetchNotifications', 'readNotifications']),
    message(item) {
      if (item.type === 'newsletter') {
        return 'A new email is subscribed'
      }
      if (item.type === 'inquiry') {
        return 'A new inquiry recieved'
      }
      return ''
    },
    route(item) {
      if (item.type === 'newsletter') {
        return '/dashboard/notifications'
      }
      if (item.type === 'inquiry') {
        return '/dashboard/notifications'
      }
      return '/dashboard/notifications'
    },
    icon(item) {
      if (item.type === 'newsletter') {
        return 'mdi-email'
      }
      if (item.type === 'inquiry') {
        return 'mdi-message'
      }
      return 'mdi-bell'
    }
  }
}
</script>

<style lang="scss" scoped>
.cursor-pointer {
  cursor: pointer;
}
</style>
